   }\n        }\n\n        mck-close-icon.mat-chip-remove.mat-chip-remove {\n            opacity: 1;\n            display: flex;\n            flex-direction: column;\n            justify-content: center;\n            align-items: center;\n        }\n\n        mat-label {\n            @include form-label;\n            font-weight: 400;\n            text-align: left;\n            margin-bottom: $spacing-xxsmall;\n        }\n\n        mat-radio-button {\n            margin-right: $spacing-small;\n        }\n\n        .mat-radio-label-content {\n            @include form-label;\n            text-align: left;\n        }\n\n        .mat-radio {\n            &-outer-circle {\n                border-color: $primary !important;\n                border-width: 1px !important;\n            }\n\n            &-inner-circle {\n                background-color: $primary !important;\n            }\n        }\n\n        mat-radio-group {\n            display: flex;\n            flex-direction: row;\n            flex-wrap: wrap;\n\n            & > mat-radio-button:last-child {\n                margin-right: 0;\n            }\n        }\n\n        &-container {\n            > .mck-form-input, > mck-select {\n                margin-bottom: $spacing-xxsmall;\n\n                @include is-screen-size(desktop) {\n                    margin-bottom: $spacing-medium-large;\n                }\n            }\n\n            .mck-radio-select {\n                margin-bottom: $spacing-small;\n\n                @include is-screen-size(desktop) {\n                    margin-bottom: $spacing-medium;\n                }\n            }\n        }\n\n        mat-chip.mat-standard-chip.mat-standard-chip {\n            border-radius: 3px;\n            min-height: 1.5rem;\n            background-color: $chip-color;\n            padding-top: $spacing-smallest;\n            padding-bottom: $spacing-smallest;\n            padding-left: $spacing-xxsmall;\n            @include chip-label;\n            @include is-screen-size(mobile) {\n                line-height: inherit;\n            }\n        }\n\n        mat-checkbox {\n            &.mat-checkbox.mat-checkbox {\n                @include checkbox-label;\n            }\n\n            div.mat-checkbox-background.mat-checkbox-background {\n                background-color: $primary;\n\n                &.mck-checkbox-disabled.mat-checkbox-disabled {\n                    background-color: $background-checkbox-disabled;\n                }\n            }\n\n            &.mck-checkbox-disabled.mat-checkbox-disabled {\n                @include checkbox-label;\n                color: $border;\n            }\n        }\n    }\n\n    &-borderless {\n        border: none;\n\n        &:hover, &:focus, &:disabled {\n            border: none;\n        }\n    }\n\n    &-select-panel {\n        margin-top: 0 !important;\n\n        mat-option {\n            padding-left: 0;\n            padding-right: 0;\n\n            &.mat-option-text {\n                @include form-label;\n                text-align: left;\n            }\n        }\n\n        &.mck-margin-top {\n            margin-top: $spacing-medium !important;\n        }\n    }\n\n    &-checkbox-panel {\n        &.mat-autocomplete-panel.mat-autocomplete-panel {\n            top: -10px;\n            max-height: 278px;\n        }\n\n        mat-option.mck-truncated {\n            & .mat-checkbox-label.mat-checkbox-label {\n                @include max-lines(2);\n            }\n        }\n\n        mat-option {\n            padding-left: 0;\n            padding-right: 0;\n            line-height: 24px;\n            height: unset;\n            min-height: 36px;\n\n            & .mat-checkbox-label.mat-checkbox-label {\n                flex: 1;\n            }\n\n            & .mat-checkbox-label.mat-checkbox-label {\n                @include checkbox-label;\n                text-align: left;\n            }\n\n            &.mck-checkbox-group-header {\n                & .mat-checkbox-